	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"FPH in the HAN","date":"19 Jun 2008","slug":"/2008-06-19-fph-in-the-han/","tags":"Y2008"},"html":"<p>A couple of weeks ago Frauke spoke at a business round table on franchising sponsored by the Wirtschaftsf&ouml;rderungsgesellschaft (business promotion council) for our region. The <a href=\"http://www.han-online.de/\">Harburger Anzeigen und Nachrichten</a> published <a href=\"http://www.papascott.de/wordpress/wp-content/uploads/2008/06/han-franchise.pdf\" title=\"han-franchise.pdf\">article on her presentation</a> (scanned PDF, in German) in their business section, complete with a photo of our <a href=\"http://www.papascott.de/archives/2007/12/14/flying-saucer/\">McCaf&eacute; Ford Ka</a>.</p>\n<p><a href=\"http://www.papascott.de/wordpress/wp-content/uploads/2008/06/han-franchise.pdf\" title=\"han-franchise.pdf\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2008/06/han-franchise.gif\" alt=\"han-franchise.gif\" border=\"0\" width=\"480\" height=\"216\" /></a></p>"},"next":{"fields":{"title":"Placeholder","date":"18 Jun 2008","slug":"/2008-06-18-placeholder/"}},"previous":{"fields":{"title":"Flags","date":"25 Jun 2008","slug":"/2008-06-25-flags/"}}},{"node":{"fields":{"title":"Placeholder","date":"18 Jun 2008","slug":"/2008-06-18-placeholder/","tags":"Y2008"},"html":"<p>The last thing I expected when we went to a McDonald's meeting the past two days was to be berated for not updating my blog for over a week. But that's what happened, at the bar after the disappointing German game against Austria at Euro 2008. </p>\n<p>So for the McDonald's insiders who follow our restaurant via PapaScott... we just got our second ISC for the month, and while the TET was a bit high (so no perfect score), we have a zero CSO for the fourth month in a row. </p>"},"next":{"fields":{"title":"Euro 2008 Flag Count","date":"06 Jun 2008","slug":"/2008-06-06-euro-2008-flag-count/"}},"previous":{"fields":{"title":"FPH in the HAN","date":"19 Jun 2008","slug":"/2008-06-19-fph-in-the-han/"}}},{"node":{"fields":{"title":"Euro 2008 Flag Count","date":"06 Jun 2008","slug":"/2008-06-06-euro-2008-flag-count/","tags":"Y2008"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2008/06/emcarflags.jpg\" alt=\"Euro Auto Flags\" align=\"right\" />Driving in Harburg this afternoon, one day before the start of Euro 2008, here are the numbers of car-flags I noticed (German flags excluded):</p>\n<p>* POR 3<br />\n* ITA 2<br />\n* POL 2<br />\n* GRE 1<br />\n* STP 1 (St. Pauli pirate flag)</p>\n<p>(I was going to twitter this, but Twitter is down, and this is too important not to post. :-)</p>"},"next":{"fields":{"title":"Uncle Tom in the 21st Century","date":"05 Jun 2008","slug":"/2008-06-05-uncle-tom-in-the-21st-century/"}},"previous":{"fields":{"title":"Placeholder","date":"18 Jun 2008","slug":"/2008-06-18-placeholder/"}}}],"pathPrefix":"","first":false,"last":false,"index":74,"pageCount":1101}}

